.mainComp{
    margin-left: 75px;
    margin-top: 40px;
    border-bottom: 1px solid #DBDEF0;
    width: 900px;
    margin: auto;
}

.questionButton{
    height: 19px;
    width: 500x;
    color: #293264;
    font-family: Karla;
    font-size: 16px;
    font-weight: 700;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: left;


}
.answerButton{
    height: 40px;
    width: "fit-content";
    padding: 5px 15px;
    border-radius: 8px;
    margin-right: 13px;
    text-align: center;
    border: 1px solid #4D5B9E;
    margin-bottom: 30px;
    font-family: Inter;
    font-size: 11px;
    font-weight: 500;
    line-height: 12px;
    letter-spacing: 1px;
    text-align: center;

}
.checkButton{
    background-color:#4D5B9E;
    color:#F5F7FB;
    padding: 10px 20px;
    border-radius: 10px;
    border-color: #4D5B9E;
    font-family: Inter;
    font-size: 14px;
    font-weight: 600;
    line-height: 12px;
    letter-spacing: 0em;
    text-align: center;
    margin: auto;
}

.scoreBtn{
    margin-top: 30px;
    font-family: Inter;
    font-size: 16px;
    font-weight: 700;
    line-height: 15px;
    letter-spacing: 0em;
    text-align: center;
    color: #293264;
}

.newCheckButton{
    background-color:#4D5B9E;
    color:#F5F7FB;
    padding: 10px 20px;
    border-radius: 10px;
    border-color: #4D5B9E;
    font-family: Inter;
    font-size: 14px;
    font-weight: 600;
    line-height: 12px;
    text-align: center;
    margin-left: 30px;
}

.results {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    margin-top: 20px;
    text-align: center;
}

.split {
    display: flex;
}

.no-notes {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: whitesmoke;
}

.first-note {
    cursor: pointer;
    background-color: #4A4E74;
    border: none;
    color: white;
    border-radius: 8px;
    padding: 20px 50px;
    font-family: Inter;
    font-size: 16px;
    font-weight: 500;
    line-height: 19px;  
    text-align: center;
}

.startBtn{
    font-family: Inter;
    font-size: 86px;
    font-weight: 500;
    line-height: 19px;
    text-align: center;
}